
::-webkit-scrollbar {
    width: 0px;
    background: transparent; /* make scrollbar transparent */
}
body, html {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
}
body {
  background: white url(/static/img/Infant_Stars_in_Orion.1da151e.png) no-repeat fixed center;
  background-size: cover;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* overflow: scroll; */
  /* overflow-x: hidden; */
}
div {
  width: inherit;
}
ul {
  background-color: transparent;
  padding: 0px;
  margin: 0px;
  list-style-type: none;
}
li {
  display: inline-block;
  background-color: transparent;
  /* backdrop-filter: blur(4px); */
  color: white;
}
ul li a {
  text-decoration: none;
  display: block;
  background-color: transparent;
  color: white;
}
#app {
  /* background-color: #2c3e50; */
  height: 100%;
}
.top {
  z-index: 50 !important;
}
.middle {
  z-index: 0 !important;
}
.card {
  background-color: rgba(255,255,255, 0.25); /* Black w/opacity/see-through */
  border-radius: 15px;
  padding: 20px;
}
.center-card {
  position: relative;
  margin: 0px auto;
  width: 50vw;
  text-align: center;
}
.page-body {
  height: 100%;
  height: 400px;
  height: calc(100vh - 140px - 40px - 40px - 45.6px); /* calc(viewport height - 140px - contacts - navbar - header text) */
  /* height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  height: calc(calc(calc(calc(var(--vh, 1vh) * 100) - 120px) - 90px) - 2em); */
  overflow-y: auto;
}
.about-page-body {
  height: 300px;
  height: 240px;
  height: calc(100vh - 140px - 40px - 40px - 45.6px - 297px); /* calc(viewport height - 140px - contacts - navbar - header text - shapeshifter) */
}
@media screen and (max-width: 991px) {
.page-body {
    height: 100%;
    height: 400px;
    height: calc(100vh - 140px - 40px - 90px - 45.6px); /* calc(viewport height - 140px - contacts - navbar - header text) */
    /* height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
    height: calc(calc(calc(calc(var(--vh, 1vh) * 100) - 120px) - 90px) - 2em); */
    overflow-y: auto;
}
.about-page-body {
    height: 300px;
    height: 240px;
    height: calc(100vh - 140px - 40px - 90px - 45.6px - 150px); /* calc(viewport height - 140px - contacts - navbar - header text - shapeshifter) */
}
}
.text {
  background-color: transparent;
}
.header {
  font-family: LemonMilk;
  font-weight: bold;
  color: white;
}
.sub-header {
  font-family: 'GlacialIndifference';
  font-weight: bold;
  color: white;
}
.accent-text {
  font-family: CaviarDreams;
  font-weight: bold;
  color: white;
}
.accent-link {
  font-family: CaviarDreams;
  font-weight: bold;
  color: #42b983;
}
.accent-link:hover {
  color: #76e6b3;
}
.body-text {
  font-family: CaviarDreams;
  color: white;
}
.route-link {
  font-size: 110%;
  word-spacing: 2px;
  letter-spacing: 1px;
}
.route-link:hover {
  color: #76e6b3;
}
.current-route-link {
  color: #42b983;
}
.text-positioning {
  background-color: transparent;
  position: relative;
  /* left: 25%; */
  width: 100%;
  margin: 0px;
}
.text-spacing {
  margin-bottom: 20px
}
@-webkit-keyframes textFadeIn {
0%   { opacity: 0;
}
100% { opacity: 1;
}
}
@keyframes textFadeIn {
0%   { opacity: 0;
}
100% { opacity: 1;
}
}
@-webkit-keyframes textFadeOut {
0%   { opacity: 0;
}
100% { opacity: 1;
}
}
@keyframes textFadeOut {
0%   { opacity: 0;
}
100% { opacity: 1;
}
}
@-webkit-keyframes textMoveIn {
0%   { left: -85%;
}
100% { left: 0%;
}
}
@keyframes textMoveIn {
0%   { left: -85%;
}
100% { left: 0%;
}
}
@-webkit-keyframes textMoveOut {
0%   { left: 0%;
}
100% { left: -85%;
}
}
@keyframes textMoveOut {
0%   { left: 0%;
}
100% { left: -85%;
}
}
